import React, { useState, useEffect } from "react";

import articleContent from "../content/article-content";
import GenericList from "../components/GenericList";
import NotFoundPage from "../pages/NotFoundPage";
import CommentsList from "../components/CommentsList";
import UpvoteSection from "../components/UpvoteSection";
import AddCommentForm from "../components/AddCommentForm";

const ArticlePage = ({ match }) => {
  const name = match.params.name;
  const article = articleContent.find((article) => article.name === name);

  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

  //use effect cant be async so work around is the below
  useEffect(() => {
    const fetchData = async () => {
      // const result = await fetch(`http://localhost:8000/api/articles/${name}`); gives cors error add proxy to package.json
      const result = await fetch(`/api/articles/${name}`);
      console.log(result);
      const body = await result.json();
      setArticleInfo(body);
    };
    fetchData();
    // setArticleInfo({ upvotes: 3 });
  }, [name]);

  if (!article) return <NotFoundPage />;

  const otherArticles = articleContent.filter(
    (article) => article.name !== name
  );

  return (
    <>
      <h1>{article.title}</h1>
      <UpvoteSection
        articleName={name}
        upvotes={articleInfo.upvotes}
        setArticleInfo={setArticleInfo}
      />
      {article.content.map((paragraph, key) => (
        <div key={key}>{paragraph}</div>
      ))}
      <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
      <CommentsList comments={articleInfo.comments} />
      <h3>Other Articles</h3>
      <GenericList articles={otherArticles} />
    </>
  );
};

export default ArticlePage;
