import React from "react";

const CommentsList = ({ comments }) => {
  // for some reason you may need parentheses instead of brackets no idea why seems brackets requires a return
  return (
    <>
      <h3>Comments:</h3>
      {comments.map((comment, key) => (
        <div key={key}>
          <h4>{comment.username}</h4>
          <p>{comment.text}</p>
        </div>
      ))}
    </>
  );
};

export default CommentsList;
